	export default {
		data() {
			return {
				indicatorDots:true,
				autoplay:true,
				interval:5000,
				duration:500,
				slides:[],
				vehicles:[]
			}
		},
		onLoad() {
           this.getindex();
		   this.getvehicles();
		},
        onShareAppMessage() {
			return {
				title: "汽车",
				path: "pages/index/index"				
			}

        },
		methods: {
           getindex() {
			   uni.request({
			   	url:"https://web58.top/zt/json/nhqc/slides.json",
				success: (res) => {
					//console.log(res.data.slides)
					this.slides =res.data.slides
				}
			   })
		   },
		   getvehicles() {
			   uni.request({
			   	url:"https://web58.top/zt/json/nhqc/vehicles.json",
				success: (res) => {
					//console.log(res.data.vehicles)
					this.vehicles =res.data.vehicles
				}
			   })			   
		   },
		   yuyue() {
			 uni.showToast({
				title: '暂不支持',
				duration: 2000,
				icon: "none",
				position:"bottom"
			 })  
		   },
		   todetails(id) {
			   uni.navigateTo({
			   	  url:'/pages/details/details?id=' + id
			   })
		   }
		}
	}
